.stackWrapper{
    background-color: #111;
    width: 100vw;
    height: 200vh;
    max-width: 100%;
    color: white;
    margin-top: 0;
    align-content: center;
}

.stackWrapper h1{
    margin-bottom: 1rem;
    text-align: left;
    margin-left: calc(50%);
    transform: translateX(-50%);
}

.stackWrapper p{
    margin-bottom: 2rem;
    text-align: left;
    margin-left: calc(50%);
    transform: translateX(-50%);
    color: gray;
}

.stack{
    align-items: center;
    justify-content: center;
    display: flex;
    margin-bottom: 170px;
}

.stackComponents{
    grid-template-columns: repeat(4, 0.5fr);
    grid-template-rows: repeat(2, 0.5fr);
    width: 50%;
    height: 50%;
    display: grid;
    gap: 1rem;
}

.technology{
    display: flex;
    width: 14rem;
    height: 6rem;
    background-color: rgb(71, 70, 70);
    border-radius: 10px;
}

.technology:hover {
    transform: translateY(-0.1rem);
    box-shadow: 0 4px 10px rgba(255, 255, 255, 0.1);
    background-color: rgb(91, 91, 92);
}

.technologyImg{
    width: 3rem;
    height: 3rem;
    border-radius: 10px;
    margin: auto 1rem;
    justify-content: center;
    display: flex;
    align-items: center;
}

.technology img{
    width: 3rem;
    height: 3rem;
}

.technology h2{
    text-align: left;
    margin-bottom: 0;
}

.technology span{
    color:rgb(209, 209, 209);
    font-size: 0.7rem;
    text-wrap: nowrap;
    text-align: left;
}

.technologyDescription span{
    text-align: left;
}

.stackComponents h1{
    margin: 0;
}

.portfolio{
    text-align: left;
    margin-left: calc(50%);
    transform: translateX(-50%);
}

.portfolio h2{
    font-size: 2rem;
}

.projects{
    grid-template-columns: repeat(2, 0.5fr);
    grid-template-rows: repeat(2, 0.5fr);
    gap: 40px;
    width: 50%;
    height: 50%;
    display: grid;
    gap: 5rem;
}

.project{
    width: 25rem;
    height: 26rem;
    background-color: #313131;
    border-radius: 10px;
    align-content: center;
    padding: 20px;
}

.project h3{
    text-align: left;
    font-size: 25px;
}

.project span{
    margin-right: 0.7rem;
    background-color: #5c5b5b;
    padding: 7px;
    border-radius: 10px;
    font-size: 13px;
    color: #c0bebe;
    margin-bottom: 20px;
    line-height: 3.5em;
    word-break: break-all;
}

.project img{
    width: 100%;
    height: 15rem;
}

.spanWrapper{
   gap: 10px;
   height: 130px;
}

.projectContent{
    display: flex;
    justify-content: space-between; 
}

.projectInfo{
    display: flex;
    justify-content: space-between;
    width: 100%; 
}

.projectLink{
    align-self: center;
    background: #494949;
    border-radius: 10px;
    width: 7rem;
    height: 1.7rem;
    display: flex;
    justify-content: center;
    padding: 1px;
    text-wrap: nowrap;
}

.projectLink img{
    width: 2rem;
    height: 2rem;
    align-self: center;
}

.projectLink h4{
    font-size: 0.7em;
}

.projectLink:hover{
    cursor: pointer;
}

.projectLink button {
        background-color: #222;
        color: white;
        width: 100%;
        border: 1px solid rgb(85, 84, 84);
        border-radius: 5px;
        cursor: pointer;
        transition: background 0.3s ease-in-out;
        display: flex;
        align-items: center;
        gap: 3px;
  }
  
  .projectLink:hover {
    background-color: #615f5f;
  }

@media (max-width: 1440px) {
    .stackComponents{
        grid-template-columns: repeat(3, 0.5fr);
        grid-template-rows: repeat(3, 0.5fr);
        width: 50%;
        height: 50%;
        display: grid;
        gap: 1rem;
    }

    .projects{
        grid-template-columns: repeat(2, 0.5fr);
        grid-template-rows: repeat(3, 0.5fr);
        gap: 40px;
        width: 50%;
        height: 50%;
        display: grid;
        gap: 1rem;
    }

    .project{
        width: 25rem;
        height: 26rem;
        background-color: #313131;
        border-radius: 10px;
        align-content: center;
        padding: 20px;
    }

    .stackWrapper{
        background-color: #111;
        width: 100vw;
        height: 100%;
        max-width: 100%;
        color: white;
        margin-top: 0;
        align-content: center;
    }
  }

  @media (max-width: 1230px) {
    .projects{
        grid-template-columns: repeat(1, 0.5fr);
        grid-template-rows: repeat(3, 0.5fr);
        gap: 40px;
        width: 50%;
        height: 50%;
        display: grid;
        gap: 1rem;
    }

    .project{
        width: 30rem;
        height: 30rem;
        background-color: #313131;
        border-radius: 10px;
        align-content: center;
        padding: 20px;
    }
  }

  @media (max-width: 1024px) {
    .stackComponents{
        grid-template-columns: repeat(2, 0.5fr);
        grid-template-rows: repeat(4, 0.5fr);
        width: 50%;
        height: 50%;
        display: grid;
        gap: 1rem;
    }
  }

  @media (max-width: 780px) {
    .stackComponents{
        grid-template-columns: repeat(2, 0.5fr);
        grid-template-rows: repeat(4, 0.5fr);
        width: 50%;
        height: 50%;
        display: grid;
        gap: 1rem;
    }

    .project{
        width: 25rem;
        height: 26rem;
        background-color: #313131;
        border-radius: 10px;
        align-content: center;
        padding: 20px;
    }
  }

  @media (max-width: 660px) {
    .stackComponents{
        grid-template-columns: repeat(1, 0.5fr);
        grid-template-rows: repeat(5, 0.5fr);
        width: 50%;
        height: 50%;
        display: grid;
        gap: 1rem;
    }
  }





